var app = getApp().globalData
import Dialog from '../../wxcomponents/vant/dist/dialog/dialog';
export default {
	data() {
		return {
			inputShowed:false,
			place:'请输入您的想法吧',
			reply_id:'',
		    user:'',
		    n:1000000,
		    msgShow:false,
		    item:{},
		    kind:'',
		    year:'',
		    time1:'',
		    time2:'',
		    id:'',//帖子的id
		    detail: {},
		    isPraise: false,
		    thought: '',
		    isFinish: false,
		    comments: [], // 评论列表
		    upvotes: 0,
		    avatar: [],
		    isDetail: false, // 可以查看
		    isDelete: false // 被删除了
		}
	},
	onShow() {
		this.getList()
		this.getData()
	},
	onLoad(e) {
		uni.showLoading({
		    title: '加载中'
		});
		this.id = e.id
		this.user = uni.getStorageSync("userId")
	},
	onShareAppMessage:function(res) {
		this.$api.reports_transpond({
			token: uni.getStorageSync("token"),
			user_id: uni.getStorageSync("userId"),
			id:this.item.id
		}).then(res1=>{
			this.item.transpond++
		})
	    if (res.from === 'button') {// 来自页面内分享按钮
			return {
			  title: res.target.dataset.title?res.target.dataset.title.substring(0,30)+'...':'德申汇资讯圈详情',
			  imageUrl:res.target.dataset.img?res.target.dataset.img:app.shareImg,
			  path: '/pagesMy/InfoDetail/InfoDetail?id='+res.target.dataset.id
			}
	    }
		if(this.item.kind == 0){
			return {
			  title: this.item.content?this.item.content.substring(0,30)+'...':'资讯圈详情',
			  imageUrl:this.item.images[0].src?this.item.images[0].src:app.shareImg,
			  path: '/pagesMy/InfoDetail/InfoDetail?id='+this.item.id
			}
		}else{
			return {
			  title: this.item.content?this.item.content.substring(0,30)+'...':'资讯圈详情',
			  imageUrl:this.item.cover_url?this.item.cover_url:app.shareImg,
			  path: '/pagesMy/InfoDetail/InfoDetail?id='+this.item.id
			}
		}
	    
	},
	methods: {
		reply(it,ind){
			if(uni.getStorageSync('userId')){
				this.reply_id = it.user_id
				this.inputShowed = true
			}else{
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
			}
			
		},
		// 跳转个人主页
		MyInfo(id){
			if(uni.getStorageSync('userId')){
				uni.navigateTo({
					url:'../../pagesMy/MyInfo/MyInfo?id='+id
				})
			}else{
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
			}
			
		},
		// 点赞
		praise(){
			if(uni.getStorageSync('userId')){
				if (this.item.is_upvotes) {
				       this.$api.NewsUpvotes({
				         token: uni.getStorageSync("token"),
				         user_id: uni.getStorageSync("userId"),
				         id: this.item.id
				       }).then(res => {
							  this.item.is_upvotes = false
							  this.item.upvotes --
							  uni.setStorageSync('unpraiseid',this.id)
							  uni.removeStorageSync('praiseid')
							  uni.showToast({
								title: res.data.msg,
								duration: 2000,
								icon:'none'
							  });
				
				       })
				     } else {
				       this.$api.NewsUpvotes({
				         token: uni.getStorageSync("token"),
				         user_id: uni.getStorageSync("userId"),
				         id: this.item.id
				       }).then(res => {
						  this.item.is_upvotes = true
						  this.item.upvotes ++
						  uni.setStorageSync('praiseid',this.id)
						  uni.removeStorageSync('unpraiseid')
						  uni.showToast({
							title: res.data.msg,
							duration: 2000,
							icon:'none'
						  });						
				       })
				     }
			}else{
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
			} 
		},
		// 展开全文
		all(it){
			it.btn = false
		},
		// 收起
		pack(it){
			it.btn = true
		},
		// 删除自己的评论
		del_zixun(it){
			if(uni.getStorageSync('userId')){
				Dialog.confirm({
				  title: '提示',
				  message:'您确定删除吗？'
				}).then(() => {
					// 删除评论
					this.$api.NewsUserDynamicsCommentsDel({
						token: uni.getStorageSync("token"),
						user_id: uni.getStorageSync("userId"),
						id:it.id
					}).then(res=>{
						if (res.data.code === '200') {
							uni.showToast({
							    title: res.data.msg,
							    duration: 2000,
								icon:'none'
							});
						  
						  this.getList()
						} else {
						  uni.showToast({
						      title: res.data.msg,
						      duration: 2000,
						  	icon:'none'
						  });
						  this.getList()
						}
						
					})
				  
				}).catch(() => {
				  
				});
			}else{
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
			}
			
				
		},
		// 举报评论
		jubao_zixun(item){
			uni.navigateTo({
				url:'../../pagesMy/Report/Report?type=2&id='+item.id
			})
		},
		// 显示举报或者删除、
		Msg(){
			if(uni.getStorageSync('userId')){
				if(this.n === 1){
					this.n = 1000000
				}else{
					this.n = 1
				}
			}else{
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
			}
			
		},
		// 跳到点赞列表
		tiao(id){
			if(uni.getStorageSync('userId')){
				uni.navigateTo({
					url:'../../pagesMy/PraiseList/PraiseList?id='+id
				})
			}else{
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
			}
			
		},
		// 发表评论
		send () {
			 if(uni.getStorageSync('userId')){
			 	if (this.thought.length && this.thought.trim().length) {
			 	  this.$api.comment_add({
			 	   token: uni.getStorageSync("token"),
			 	   user_id: uni.getStorageSync("userId"),
			 	   id: this.id,
			 	   content: this.thought,
			 						 reply_id:this.reply_id
			 	  }).then(res => {
			 	    if (res.data.code === '200') {
			 	      uni.showToast({
			 	      		title: res.data.msg,
			 	      		duration: 2000,
			 	      		icon:'none'
			 	      });
			 							this.reply_id = ''
			 	      this.thought = ''
			 	      this.getList()
			 	    } else if (res.data.code === '1073') {
			 	      uni.showToast({
						title: res.data.msg,
						duration: 2000,
						icon:'none'
			 	      });
			 	    }
			 	  })
			 	  // }
			 	} else {
					   uni.showToast({
							title: '评论内容不可为空',
							duration: 2000,
							icon:'none'
					   });
			 	}
			 }else{
			 	uni.clearStorageSync()
			 	uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
			 	uni.showToast({
			 	    title: '请登录',
			 	    duration: 2000,
			 		icon:'none'
			 	});
			 	setTimeout(function() {
			 		uni.reLaunch({
			 			url: '/pages/login/login'
			 		})
			 	}, 1000);
			 }
		      
		},
		// 评论跳转个人首页
		info(id){
			if(uni.getStorageSync('userId')){
				uni.navigateTo({
					url:'../../pagesMy/MyInfo/MyInfo?id='+id
				})
			}else{
				uni.clearStorageSync()
				uni.setStorageSync('path',this.$url.getCurrentPageUrlWithArgs())
				uni.showToast({
				    title: '请登录',
				    duration: 2000,
					icon:'none'
				});
				setTimeout(function() {
					uni.reLaunch({
						url: '/pages/login/login'
					})
				}, 1000);
			}
			
		},
		// 图片预览
		preview(ind,img){
			 var arr = []
			 img.forEach((item,index)=>{
				 arr.push(item.src)
			 })
			 
			  uni.previewImage({
				 urls: arr,
				 current:ind
			 });
		},
		// 获取详情数据
		   getData () {
		      this.$api.NewsUserDynamicsDetail({
		        token: uni.getStorageSync("token")|| '',
		        user_id: uni.getStorageSync("userId")|| '',
		        id: this.id
		      }).then(res => {
		        if (res.data.code === '200') {
					if(res.data.data.content.length>63){
						res.data.data.btn = true
					}else{
						res.data.data.btn = false
					}
				  this.item = res.data.data
		          this.detail = res.data.data // 信息
		          this.upvotes = res.data.data.upvotes // 点赞数量
		          this.avatar = res.data.data.upvotes_avatar // 头像列表
		          this.isPraise = res.data.data.is_upvotes // 是否点赞
				  this.kind = res.data.data.kind
				  uni.hideLoading();
		       
		        } else if (res.data.code === '1000') {
		          uni.clearStorageSync()
		          uni.showToast({
		              title: '登录过期，请重新登录',
		              duration: 2000,
		          	icon:'none'
		          });
		          
		          uni.reLaunch({
		          	url: '../../pages/login/login'
		          })
		        }
		      })
		    },
		// 获取评论列表
		getList () {
		      this.$api.comment_list({
		        token: uni.getStorageSync("token"),
		        user_id: uni.getStorageSync("userId"),
		        id: this.id
		      }).then(res => {
		        this.comments = res.data.data
		      })
		},
	  // 删除自己的资讯圈
	  del_zixun2(it){
	  	Dialog.confirm({
	  	  title: '提示',
	  	  message:'您确定删除吗？'
	  	}).then(() => {
	  		this.$api.del_user_dynamic({
	  			token: uni.getStorageSync("token"),
	  			user_id: uni.getStorageSync("userId"),
	  			dynamic_id:it.id
	  		}).then(res=>{
	  			uni.showToast({
	  			    title: res.data.msg,
	  			    duration: 2000,
	  				icon:'none'
	  			});
	  			// this.listtui = this.listtui.filter((item,index) => item.id != it.id)
	  			// this.listguan = this.listguan.filter((item,index) => item.id != it.id)
	  			// this.listv = this.listv.filter((item,index) => item.id != it.id)
				if (res.data.code === '200') {
					  uni.setStorageSync('delid',it.id)
					  uni.switchTab({
						url:'../../pages/information/information'
					  })
				}
	  		})
	  	  
	  	}).catch(() => {
	  	  
	  	});
	  		
	  },
	  // 举报资讯圈
	  jubao_zixun2(item){
	  	uni.navigateTo({
	  		url:'../../pagesMy/Report/Report?type=1&id='+item.id +'&abc=5'
	  	})
	  },
	}
}
